<template>
    <div class="bg_intro">
      <div class="intro">
        <ul class="info">
          <li>
            <input type="text" id="name" placeholder="用户名" />
          </li>
          <!-- <li>
          <input type="email" id='email' placeholder="电子邮件">
        </li> -->
          <li>
            <input type="password" id="password" placeholder="密码" />
          </li>
          <!-- <li>
          <input type="password" id='SecPass' placeholder="再次确认密码">
        </li> -->
        </ul>
<!--        <router-link :to="{ name: 'home' }"-->
<!--          ><button id="btn">登 &nbsp;&nbsp; 录</button>-->
<!--        </router-link>-->
        <button id="btn" @click="login">登 &nbsp;&nbsp; 录</button>
        <router-link :to="{ name: 'Register' }"
          ><a>No account? Click here to Register</a></router-link
        >
      </div>  
    </div>
    <!-- <router-view></router-view> -->
</template>

<script>
import { useWebSocket } from '../../webSocket/websocket';
import { useRouter } from 'vue-router';
export default {
  setup () {
    const ws = useWebSocket(handleMessage);
    const router = useRouter();
    const login = () => {
      let uname = document.getElementById("name");
      let pw = document.getElementById("password");
      ws.send(JSON.stringify({
        usename: uname,
        password: pw
      }));
    }
    function handleMessage(e) {
      router.push('/');
    }
    return{
      login
    }
  }
};
</script>

<style scoped>
* {
  padding: 0;
  margin: 0;
}
li {
  list-style: none;
}
template,
body {
  text-align: center;
}
.bg_intro {
  height: 100%;
  width: 100%;
}
.bg_intro .intro {
  position: relative;
  background: #a2a2a298;
  border-radius: 20px;
  width: 30%;
  height: 65%;
  margin: 0 auto;
  margin-top: 5%;
}
.bg_intro .intro .info {
  position: relative;
  width: 80%;
  height: 300px;
  margin: auto auto;
  padding: 30px 0px 0px 0px;
}
input[type="text"],
input[type="password"],
textarea {
  display: block;
  width: 80%;
  height: 15px;
  padding: 12px;
  border-radius: 5px;
  background: #fff;
  border: none;
  outline: none;
  margin: 60px auto;
  text-transform: capitalize;
  color: #383838;
  font-size: 14px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
  font-family: "roboto", sans-serif;
}

::placeholder {
  color: rgba(46, 31, 31, 0.6);
}
#btn {
  /* position: relative; */
  width: 80%;
  height: 40px;
  line-height: 40px;
  margin: auto;
  background: #383838;
  color: #fff;
  font-weight: bold;
  border-radius: 5px;
  text-transform: capitalize;
  border: none;
  cursor: pointer;
  display: block;
  margin: -10px 45px;
}
a {
  /* position: relative; */
  font-size: 15px;
  text-align: center;
  display: inline-block;
  text-transform: capitalize;
  color: #1e2f92;
  font-weight: bold;
  padding: 10px 0px 0px 60px;
}
</style>